/*------------------------------------*\
    ::Back Home Link
\*------------------------------------*/
.basic-col {
    &__dark {
        width: 33.33%;
        padding: $gap;
        @include bp($tab--max){
            width: 50%;
        }
        @include bp($phone--max){
            width: 100%;
        }
    }
    &__back-home {
        position: relative;
        display: inline-block;
        font-family: $fam--2;
        padding: 1em .5em 1em 3em;
        // &[class], /* DEBUG */
        &:hover {
            span {
                @extend %icon--over;
                position: absolute;
                display: block;
                left: 0;
                width: 2.5em;
                height: 2.5em;
                border-radius: 50%;
                transform: translateY(-50%);
                &:before { // house roof
                    left: .5em;
                    background-color: darken($color-1-dark, 5%);
                    border-color: $color-1-light;
                    transform: rotate(45deg) translate(-.4em, -.5em);
                    transition: all 200ms, background-color 200ms 250ms;
                }
                &:after { // house frame
                    height: .6em;
                    background-color: darken($color-1-dark, 5%);
                    border-color: $color-1-light;
                    transition: all 200ms, height 200ms 200ms, border 200ms 200ms;
                    transform: translate(.5em, 0) rotate(0);
                }
            }
        }
        span {
            @extend %icon--up;
            position: absolute;
            display: block;
            left: 0;
            width: 2.5em;
            height: 2.5em;
            border-radius: 50%;
            transform: translateY(-50%);
            &,
            &:before,
            &:after {
                position: absolute;
                top: 50%;
                display: block;
            }
            &:before,
            &:after {
                content: "";
                width: 1em;
                height: 1em;
            }
            &:before { // house roof
                content: "";
                left: .5em;
                border: 0 solid $color-2;
                border-width: .2em 0 0 .2em;
                transform: rotate(-45deg) translate(0.5em, -0.2em);
                transition: all 200ms, background-color 150ms;
            }
            &:after { // house frame
                background-color: transparent;
                height: 0em;
                width: 1.1em;
                border: 0 solid transparent;
                border-width: 0 1px 1px;
                transform: translate(.5em, 0) rotate(-45deg);
                transition: all 200ms, height 0ms 200ms;
                z-index: -1;
            }
        }
    }
}